<template>
  <button
    class="h-button"
    :disabled="disabled && loading"
    :autofocus="autofocus"
    :type="nativeType"
    :class="[
              type ? 'h-button-' + type : '',
              size ? 'h-button-' + size : '',
              shape? 'h-button-' + shape : '',
              {
                'loading': loading,
                'active': active
              }]"
    @click="handleClick"
  >
    <i v-if="loading && type !== 'text'" class="iconfont icon-loading h-button-loading"></i>
    <i v-if="icon && !loading" :class="['iconfont', 'icon-' + icon]" class="h-button-icon"></i>
    <span v-if="$slots.default">
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {
  name: "HButton",
  props: {
    type: {
      // default/primary/info/success/warning/danger/text
      type: String,
      required: false,
      default: "default"
    },
    size: {
      // mini/small/large
      type: String,
      required: false
    },
    disabled: {
      // 
      type: Boolean,
      required: false,
      default: false
    },
    loading: {
      // 
      type: Boolean,
      required: false,
      default: false
    },
    active: {
      // 
      type: Boolean,
      required: false,
      default: false
    },
    shape: {
      // circle/round
      type: String,
      required: false,
      default: ""
    },
    autofocus: {
      // 
      type: Boolean,
      required: false,
      default: false
    },
    icon: {
      // 
      type: String,
      required: false
    },
    nativeType: {
      // button/submit/reset
      type: String,
      required: false,
      default: "button"
    }
  },
  data() {
    return {};
  },
  methods: {
    /**
     * @method: handleClick
     * @description: emit clickevent
     * @param {Object} ev 
     * @return: {undefined}
     * @author: Dadaiwei
     */
    handleClick(ev) {
      this.$emit("click", ev);
    }
  }
};
</script>